import React from "react";
import "../layouts/form.css";

export class Header extends React.Component {
  render() {
    const actived = {
      color: "#525866",
      fontWeight: "bold"
    };

    return (
      <div className="nav">
        <div className="nav-content">
          <a href="http://www.zocdesign.net/"  target="_blank" rel="noopener noreferrer">
            <div className="image" />
          </a>
          <div>
            <div className="indicator"><div style={this.props.progress} ></div></div>
            <ul className="module">
              <li style={this.props.activedA} ></li>
              <li style={this.props.activedB} ></li>
              <li style={this.props.activedC} ></li>
              <li style={this.props.activedD} ></li>
              <li style={this.props.activedE} ></li>            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;



